import './App.css';
import {v4 as UID} from 'uuid';
import { useState, useEffect } from 'react';

function App() {
  const [name, setName] = useState("");
  const [subAdd, setSubAdd] = useState(false);
  const [loading, setLoading] = useState(false);
  const [isLoaded, setIsLoaded] = useState(false);
  const [newOperation, setNewOperation] = useState({name: "", cost: "", md: "", d: ""});
  const [selOperation, setSelOperation] = useState({id: "", name: "", cost: "", md: "", d: ""});
  const [operations, setOperations] = useState([{ id: UID(), name: "Name of operation", cost: "100", md: "011", d: "210" }]);

  useEffect(() => {
    if (isLoaded)
      localStorage.setItem("operations", JSON.stringify(operations));
  }, [operations])  

  useEffect(() => {
    if (isLoaded)
      localStorage.setItem("name", name);
  }, [name])

  useEffect(() => {
    if (localStorage.getItem("operations") != null)
      setOperations(JSON.parse(localStorage.getItem("operations")));
    if (localStorage.getItem("name") != null) 
      setName(localStorage.getItem("name"));
    setIsLoaded(true);
  }, [])

  let operationindex = 1;

  async function createOne() {
    setLoading(true);
    let n = name || "";
    let counter = 0;
    
    while (n.length < 5 && counter < 10){
      n = prompt("Musite vyplnit nazev, ktery je alespon 5 znaku :D");
      if (!n) n = "Err"
      counter++
    }
    if (counter == 10) return alert("Název nesplňuje podmínky");
    // eslint-disable-next-line no-restricted-globals
    const yes = confirm("Jste si jisty ze chcete tuto sablonu pouzit?");
    if (!yes) return;
    const auth = await fetch("/create", {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        operations: operations,
        name: n
      })
    });
    setLoading(false);
    if (auth.status == 404) 
      alert("Tato aplikace nemuze bezet bez serveru, ktery ma za ucelem pripojeni k databazi,.. prosim pripojte se k serveru... nebo zkontrolujte pripojeni k internetu.");
    else if (auth.status == 403) 
      alert("Přístup odmítnut vaše autorizace není dostatečná.");
    else if (auth.status == 200) {
      setTimeout(()=> {
        alert("Vas vytvor byl ulozen do databaze a budete presmerovani na hlavni stranku");
        setOperations([]);
        setName("");
        window.location.href="/";
      }, 1000);
    }
    else alert("Neco se pokazilo pri pokusu o navazani pripojeni k serveru...");
  }

  function isItTrueNumber(value, min, type) {
    let setto = value[Object.keys(value)[0]];
    if (setto == "") setto = "0";
    
    if ( !( String(setto).match(/^[0-9]+$/) && setto.length >= min)) return false;
    
    if (type == "select") setSelOperation({...selOperation, ...value});
    else setNewOperation({...newOperation, ...value});
    return;
  }

  function isItBetween(value, min, max, type) {
    let setto = value[Object.keys(value)[0]].length;
    return setto <= max && setto >= min ? isItTrueNumber(value, min, type): false;
  }

  function add() {
    for (const properties of Object.values(newOperation))
      if (properties.length < 1) return;
    
    setOperations([
      ...operations, {
        ...newOperation, id: UID()
      }
    ])
    setNewOperation({name: "", cost: "", md: "", d: ""})
  }
  
  function update() {
    for (const properties in Object.values(selOperation))
      if (properties.length < 1) return console.log("fuck you");
    
    let id;
    for (let i = 0; i < operations.length; i++) 
      if (operations[i].id == selOperation.id) id = i;
      
    let operationsEdit = operations;
    if (typeof id == "number") {
      operationsEdit[id] = selOperation
      setOperations(operationsEdit)
      setSelOperation({id: "", name: "", cost: "", md: "", d: ""})
      setSubAdd(false);
    }
  }

  function setSelect(e) {
    const r = e.target.children;
    if (e.target.parentElement.children[5])
      setSelected(e.target.parentElement.children[5].children[0].value)
    console.log(e.target.parentElement);
    setTimeout(() => {
      if (e.target.children[0]) r[0].focus()
    },100)
  }
  
  function addsub() {
    if (!selOperation.id) return;

    for (const properties of Object.values(newOperation))
      if (properties.length < 1) return;
    
    let id = 0;
    let opt = {...newOperation, id: UID()};
    let operationsEdit = operations;

    for (let i = 0; i < operations.length; i++) if (operations[i].id == selOperation.id) id = i
    operationsEdit.splice(id+1, 0, opt)
    console.log(operationsEdit);
    if (typeof id == "number") {
      setOperations(operationsEdit);
      update();
      setNewOperation({name: "", cost: "", md: "", d: ""});
    }

  }

  const updateEnter = (e) => console.log(e.key == "Enter" ? update():"");// e.key == "Enter" ? update: () => {};
  const del = (id) => setOperations([...operations.filter(o => o.id != id)])
  const setSelected = (id) => setSelOperation(operations.find(x => x.id == id));

  return (
    <div className="App bg-dark text-white text-center pb-5" onClick={update}>
      <div className="container mb-5 pb-3" onClick={e => e.stopPropagation()}>
        <table className="table table-hover table-dark ">
          <thead>
            <tr>
              <th></th>
              <th colSpan="4" className="" id="addon">
                <div className="input-group my-5">
                  <label htmlFor="name" className="input-group-text bg-secondary" aria-describedby="addon">Název: </label>
                  <input type="text" placeholder="Zadejte název" className="form-control bg-dark text-white text-center fs-4" value={name} onChange={e => setName(e.target.value)}  />
                </div>
              </th>
              <th></th>
            </tr>
            <tr>
              <th>Číslo</th>
              <th>Jméno</th>
              <th>Částka</th>
              <th>MD</th>
              <th>D</th>
              <th>{/* delete buttons */}</th>
            </tr>
          </thead>
          <tbody>
            {
              operations.map((operation, i) => {
                operationindex++;
                if (selOperation && operation.id == selOperation.id) {
                  return <>
                    <tr>
                      <td>
                        {operationindex-1}
                      </td>
                      <td className="col-7">
                        <input className="form-control bg-dark text-white text-center" type="text" 
                          value={selOperation.name} onKeyPress={e=>{updateEnter(e)}} onChange={e => setSelOperation({...selOperation, name: e.target.value})} placeholder="Název operace" />
                      </td>
                      <td className="col-2">
                        <input className="form-control bg-dark text-white text-center" type="text"
                          value={selOperation.cost} onKeyPress={e=>{updateEnter(e)}} onChange={e => isItTrueNumber({cost: e.target.value}, 0, "select")} placeholder="Částka..." />
                      </td>
                      <td className="col-1">
                        <input className="form-control bg-dark text-white text-center" type="text" 
                          value={selOperation.md} onKeyPress={e=>{updateEnter(e)}} list="account-UCTO" onChange={e => isItBetween({md: e.target.value}, 0, 3, "select")} placeholder="Ma dáti..." />
                      </td>
                      <td className="col-1">
                        <input className="form-control bg-dark text-white text-center" type="text" 
                          value={selOperation.d} onKeyPress={e=>{updateEnter(e)}} list="account-UCTO" onChange={e => isItBetween({d: e.target.value}, 0, 3, "select")} placeholder="Dal..." />
                      </td>
                      <td>
                        <button onClick={update} className="btn btn-success form-control">&#8634;</button>
                      </td>
                    </tr>
                    <tr hidden={subAdd}>
                      <td></td>
                      <td colSpan="4"><button onClick={e => setSubAdd(true)} className="form-control btn btn-secondary text-white-50">Přidat sem</button></td>
                      <td></td>
                    </tr>
                  </>
                } else {
                  return <tr onClick={e => setSelect(e)}>
                      <td>{i +1}</td>
                      <td>{operation.name}</td>
                      <td>{new Intl.NumberFormat('en-US', {
                      style: 'currency',
                      currency: 'USD',
                      maximumFractionDigits: 0
                    }).format(operation.cost).replace(/,/g, " ")}</td>
                      <td>{operation.md}</td>
                      <td>{operation.d}</td>
                      <td className="justify-content-between d-flex pb-1" >
                        <button value={operation.id} onClick={e => del(e.target.value)} className="form-control btn btn-danger">X</button>
                      </td>
                    </tr>
                }
              })
            }
          </tbody>
          <tfoot>
            <tr>
              <td>
                {operationindex}
              </td>
              <td className="col-7">
                <input className="form-control bg-dark text-white text-center" type="text" 
                  value={newOperation.name} onChange={e => setNewOperation({...newOperation, name: e.target.value})} placeholder="Název operace..." />
              </td>
              <td className="col-2">
                <input className="form-control bg-dark text-white text-center" type="text"
                  value={newOperation.cost} onChange={e => isItTrueNumber({cost: e.target.value}, 0)} placeholder="Částka..." />
              </td>
              <td className="col-1">
                <input list="account-UCTO" className="form-control bg-dark text-white text-center" type="text" 
                  value={newOperation.md} onChange={e => isItBetween({md: e.target.value}, 0, 3)} placeholder="Ma dáti..." />
              </td>
              <td className="col-1">
                <input list="account-UCTO" className="form-control bg-dark text-white text-center" type="text" 
                  value={newOperation.d} onChange={e => isItBetween({d: e.target.value}, 0, 3)} placeholder="Dal..." />
              </td>
              <td>
                <button onClick={subAdd ? addsub: add} className="btn btn-success form-control">+</button>
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
      <div className="flaoting" onClick={e => e.stopPropagation()}>
        <button onClick={e => 
          // eslint-disable-next-line no-restricted-globals
          confirm("Opravdu? Tato akci snaze vsechny operace a nazev...") ? setOperations([]) && setName("") : ""
          } className="fs-4 btn me-3 btn-danger">Smazat</button>
        <button onClick={createOne} className="fs-4 btn btn-primary">Vytvorit</button>
      </div>
      <div className="loading" hidden={!loading}>
        <h1>Loading...</h1>
      </div>
    </div>
  );
}

export default App;
